<template>
	<view class="register">
		<view class="formBox">
			<view class="mititle">
				<image style="width: 26rpx;margin-right: 12rpx;" mode="widthFix" src="/static/img/timi.png"></image>
				基本身份信息登记
			</view>
			<u-cell-group class="tsbox">
				<u-cell-item title="真实姓名" :value="details.universalAntiUserMain27" :arrow="false"></u-cell-item>
				<u-cell-item title="性别" :value="details.sex==1?'男':'女'" :arrow="false"></u-cell-item>
				<u-cell-item title="身份证号" :value="details.idcard" :arrow="false"></u-cell-item>
				<u-cell-item title="中国第二代居民身份证：" :arrow="false"></u-cell-item>
			</u-cell-group>
			<u-row gutter="20">
				<u-col span="6">
					<u-image width="100%" height="220rpx" :src="details.idcardFrontFiles" style="margin-top:20rpx"></u-image>
				</u-col>
				<u-col span="6">
					<u-image width="100%" height="220rpx" :src="details.idcardReverseSideFiles" style="margin-top:20rpx"></u-image>
				</u-col>
			</u-row>
		</view>
		<view class="formBox">
			<view class="mititle">
				<image style="width: 26rpx;margin-right: 12rpx;" mode="widthFix" src="/static/img/timi.png"></image>
				社会身份信息登记
			</view>
			<u-cell-group class="tsbox">
				<u-cell-item title="工作单位类型" :value="fycon(details.unitStype)" :arrow="false"></u-cell-item>
				<u-cell-item title="单位/场所名称" :value="details.unitName" :arrow="false"></u-cell-item>
				<u-cell-item title="所属辖区" :value="details.orgAddress" :arrow="false"></u-cell-item>
				<u-cell-item title="单位地点" :value="details.placeAddress" :arrow="false"></u-cell-item>
			</u-cell-group>
		</view>
		<view class="formBox" v-show="details.placeAddress">
			<view class="mititle">
				<image style="width: 26rpx;margin-right: 12rpx;" mode="widthFix" src="/static/img/timi.png"></image>
				审批
			</view>
			<u-cell-group class="tsbox">
				<u-cell-item title="审批状态" :value="details.placeAddress" :arrow="false"></u-cell-item>
				<u-cell-item title="审批意见" :value="details.placeAddress" :arrow="false"></u-cell-item>
			</u-cell-group>
		</view>
			<u-toast ref="uToast"></u-toast>
			<u-row gutter="20" v-show="!details.placeAddress">
				<u-col span="6">
					<u-button @click="tocomplete(details)" type="primary" style="margin:30rpx 20rpx;">审批通过</u-button>
				</u-col>
				<u-col span="6">
					<u-button @click="toreject(details)" type="error" style="margin:30rpx 20rpx;">审批拒绝</u-button>
				</u-col>
			</u-row>
	</view>
</template>

<script>
	import {
		getDicts,
		getzyzInfo
	} from '../../api/index.js'
	export default {
		data() {
			return {
				params:{
				},
				details: {},
				tylist: [],
			}
		},
		onLoad() {
			getDicts("unit_stype").then(res => {
				if (res.code == 200) {
					this.tylist = res.data;
				}
			})
			this.params.queryJson = [{
				data1: 'procInstId',
				data2: '0',
				data3: this.$route.query.procInstId,
				objectId: "291",
				subType: null,
				flag: false,
				fileType: "String"
			},{
				data1: 'deployId',
				data2: '0',
				data3: this.$route.query.deployId,
				objectId: "291",
				subType: null,
				flag: false,
				fileType: "String"
			}];
			this.params.queryJson = JSON.stringify(this.params.queryJson);
			
			getzyzInfo(this.params).then(res => {
				if (res.code == 200) {
					this.details = res.rows[0];
				}
			})
		},
		methods: {
			tocomplete(item) {
				item.comment = '审批同意';
				if (item.objectId == '291') {
					item.instanceId=item.procInsId;
					zyjcomplete(item).then(res => {
						if (res.code == 200) {
							this.$refs.uToast.show({
								title: '审批成功'
							});
						}
					})
				} else {
					complete(item).then(res => {
						if (res.code == 200) {
							this.$refs.uToast.show({
								title: '审批成功'
							});
						}
					})
				}
			},
			toreject(item) {
				item.comment = '审批拒绝';
				reject(item).then(res => {
					if (res.code == 200) {
						this.$refs.uToast.show({
							title: '操作成功'
						});
					}
				})
			},
			fycon(name) {
				let result = null;
				this.tylist.map((item) => {
					if (item.dictValue == name) {
						result = item.dictLabel;
					}
				})
				return result;
			},
		}
	}
</script>

<style scoped>
	.register {
		background-color: #F5F6FA;
		padding-bottom: 20rpx;
	}

	.mybox {
		text-align: center;
		margin: 50rpx 0;
	}

	.formBox {
		padding: 0 30rpx 5rpx 30rpx;
		background-color: #fff;
	}
	.tsbox /deep/ .u-cell{
		padding: 26rpx 0;
	}

	.mititle {
		font-size: 30rpx;
		font-weight: bold;
		color: #333333;
		height: 90rpx;
		line-height: 90rpx;
		margin: 0 -30rpx;
		padding: 0 30rpx;
		border-bottom: 1px solid #f1f1f1;
	}

	.tsbox /deep/ .u-cell_title {
		color: #777777;
	}

	.tsbox /deep/ .u-cell__value {
		font-size: 28rpx;
		font-weight: bold;
		color: #333333;
	}
</style>